import request from '@/utils/request'
let test = true;
export function addOrder(data) {
  if (test) {
    let res = {
      code: 200,
      message: "success"
    };
    return new Promise(resolve => {
      resolve(res)
    });
  }
  return request({
    url:'/order/add',
    method:'post',
    data:data
  });
}

export function fetchOrderList(params) {
  if (test) {
    let orderList = {
      code: 200,
      message: "success",
      data: {
        list: [{
          orderSn: "SN512231",
          customerName: "Tom",
          customerId: 1,
          totalAmount: 3505,
          orderDate: 1628472604623,
          orderOther: "此处为样例备注",
          goodList: [{
            firm: "喜马诺",
            stock: "飞轮",
            model: "HG200-9",
            price: 62,
            num: 5
          }, {
            firm: "Michelin",
            stock: "轮胎2",
            model: "215/70R15-2",
            price: 639,
            num: 5
          }]
        }, {
          orderSn: "SN222222",
          customerName: "Tom",
          customerId: 1,
          totalAmount: 3505,
          orderDate: 1628472604623,
          orderOther: "此处为样例备注2",
          goodList: [{
            firm: "喜马诺",
            stock: "飞轮2",
            model: "HG200-9-2",
            price: 62,
            num: 5
          }, {
            firm: "Michelin",
            stock: "轮胎2",
            model: "215/70R15-2",
            price: 639,
            num: 5
          }]
        }],
        total: 11
      }
    };
    return new Promise(resolve => {
      resolve(orderList)
    })
  }
  return request({
    url:'/order/list',
    method:'get',
    params:params
  });
}




